import React, { useState, useContext } from 'react';
import { Layout, Input, AutoComplete, Typography } from 'antd';
import { useHistory } from 'react-router-dom';
import { PLACEHOLDER } from '../../constants';
import './index.css';
import { getJournalListByCondition } from '../../utils/request';
import { AppContext } from '../../context'

const { Content } = Layout;

const { Title, Text } = Typography;


const Home = () => {

    const [journalList, setJournalList] = useState([]);
    const [options, setOptions] = useState([]);
    const { changeJournalData } = useContext(AppContext);

    const history = useHistory();

    const onSearch = searchText => {
        getJournalListByCondition(searchText, allJournals => {
            if (!allJournals) return;
            let listJour = allJournals && allJournals[0].result;
            setJournalList(listJour)
            let journalNameList = listJour.map(d => ({ value: d.jname }));
            setOptions(
                !searchText ? [] : journalNameList
            );
        });
    };

    const onSelect = data => {
        console.log('onSelect', data);
        let journal = journalList.filter(d => d.jname === data)[0];
        if (!journal) return;
        changeJournalData(journal);
        history.push('/journal/' + journal.journal_id);
    };

    return (
        <Content className="content-body">
            <div className="title">
                <Title className="h1" level={2}>中国科技核心期刊引证报告</Title>
                <Text>Chinese Science and Technology Journal Citation Reports</Text>
            </div>
            <div className="search">                        
                <AutoComplete
                    options={options}
                    style={{ width: '90vw', textAlign: 'left' }}
                    onSelect={onSelect}
                    onSearch={onSearch}>
                    <Input.Search size="large" enterButton placeholder={PLACEHOLDER} />
                </AutoComplete>
            </div>

        </Content>
    );

}

export default Home;